import React, { useEffect, useState } from 'react'
import { Col, Pagination, Row } from "antd"
import picUrl from "../../../img/frezzing.png"
import "./index.less"
import { useLocation } from 'react-router-dom'
const list = Array.from(Array(30), (_, i) => {
    return {
        id: i,
        src: picUrl
    }
})

function Img() {
    const location = useLocation()
    const [picList, setPicList] = useState([])
    const [pageData, setPageData] = useState({
        current: 1,
        pageSize: 10,
        total: ""
    })
    useEffect(() => {
        setPageData({
            ...pageData,
            total: list.length
        })
    }, [])

    useEffect(() => {
        let _list = JSON.parse(JSON.stringify(list))
        if (pageData.current === "1") {
            setPicList(_list.slice(0, pageData.pageSize))
        } else {
            setPicList(_list.slice(((pageData.current - 1) * pageData.pageSize), pageData.current * pageData.pageSize))
        }


    }, [pageData])

    return (
        <div className='pageing--img-container'>
            <main>
                <Row gutter={[16, 16]}>
                    {
                        picList.map(it => {
                            return (
                                <Col span={6} key={it.id}>
                                    <img src={it.src} alt="" className='pageing-img' />
                                </Col>
                            )
                        })
                    }

                </Row>

            </main>
            <footer>
                <Pagination
                    showSizeChanger
                    pageSizeOptions={[10, 20, 50, 100]}
                    pageSize={pageData.pageSize}
                    current={pageData.current}
                    total={pageData.total}
                    onChange={(page, pageSize) => {
                        setPageData({
                            ...pageData,
                            pageSize: pageSize,
                            current: page
                        })
                    }}
                />
            </footer>
        </div>
    )
}

export default Img
